.App {
  display: flex;
  flex-direction: column;
}

.Flex {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height:65em;
  width: 70em;
  align-items: center;
  margin: 2em;
}

.Navbar {
  width: 200%;
  position: fixed;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Laatikko {
  padding: 2em;
  background-color: #096484;/*#4796b3;*/
  width: 95%;
}

.laatikkoDiv {
  margin: 2em;
  width: 30em;
}

p {
  color: aliceblue;
}

p b {
  color: #52accc;
}

.Haku {
  font-weight: 100;
}

.hakuTiedot {
  margin-top: 18em;
  flex-direction: row;
}

.leaflet-container {
  margin-top: 5em;
  width: 40em;
  height: 50em;
  }

@media screen and (max-width: 1280px) {
  .App {
    width: 70em;
  }
}

@media screen and (max-width: 999px) {
  .App {
    flex-wrap: wrap;
    height: 50em;
    width: 50em;
  }

  .leaflet-container {
    margin-left: 3em;
    width: 40em;
    height: 60em;
  }

  .Hakukentta {
    margin-top: 10em;
  }
}

@media screen and (max-width: 776px) {
  .App {
    flex-wrap: column;
    align-items: center;
    margin-left: 0.1em;
    height: 80em;
    width: 40em;
  }

  .leaflet-container {
    width: 40em;
    height: 50em;
    margin-bottom: 1em;
  }

  .Laatikko {
    margin-top: 1em;
  }

  .Haku {
    margin-bottom: 1em;
    margin-top: 1em;
  }
}

.footer{
  max-height: 70px;
  height: 5em;
  margin-top: 1em;
  padding: 1em;
  background-color: rgb(181, 201, 216);
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}

body {
  margin: 0px;
}

.info {
  padding: 6px 8px;
  font: 14px/16px Arial, Helvetica, sans-serif;
  background: white;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.info h4 {
  margin: 0 0 5px;
  color: #777;
}

.legend {
  text-align: left;
  line-height: 18px;
  color: #555;
}

.legend i {
  width: 2em;
  height: 1em;
  float: left;
  margin-right: 8px;
  opacity: 0.7;
}

